const initialState = {
    name: 228,
    area: '-',
    class: '',
    numRooms: 4,
    garage: false,
    carPlaces: 2,
    price: 45000,
    rent: 350,
    isLoading: false
};

export default function house(state = initialState, action) {
    const { type, payload } = action;
    var newState;

    switch (type) {
        case 'LOAD_INFO_HOUSE':
            return payload;

        case 'SHOW_HOUSE':
            newState = { ...state };
            newState.isShow = payload;
            return newState;

        case 'SET_LOADING_HOUSE':
            newState = { ...state };
            newState.isLoading = payload;
            return newState;

        case 'ANS_BUY_HOUSE':
            newState = { ...state };
            if (payload.answer == 0) {
                newState.answer = '     ';
            }
            else if (payload.answer == 1) {
                newState.answer = '  ';
                newState.owner = payload.owner;
            } else {
                newState.answer = payload.answer;
            }

            newState.isLoading = false;
            return newState;
    }

    return state;
}